.Cart{
	width:350px;
	max-height:500px;
	background-color:white;
	position:fixed;
	z-index:1;
	float:right;
	right:0px;
	top:62px;
	border:2px solid;
	animation: pop 0.3s linear 1;
	
	
	
}
@keyframes pop{
  50%  {transform: scale(1.1);}
}
.blank{
	margin-top:20px;
}
.Cart .Title{
	font-family:"Muli";
	font-size:22px;
	padding-bottom:20px;
	padding-top:12px;
	border-bottom:1px solid black;
	background-color:#001F3F;
	color:white;
}


.closeBtn:hover{
	cursor:pointer;
}

.closeBtn{
	float:right;
	font-size:25px;
	right:6px;
	top:-1px;
	position:relative;
	color:black;
}

.closeBtn img{
	width:37px;
}


.cartProduct img{
	width:50px;
}

.cartProduct{
	position:relative;
	float:left;
	left:20px;
	margin-top:10px;
	overflow:hidden;
	top:1px;
	


	border-bottom:1px solid black;
}




.cartItemName{
	position:relative;
	display:inline-block;
	font-family:"Muli";
	font-weight:700;
	left:-5px;

	width:210px;
	top:-20px;
}
.closeBtn2{
	position:relative;
	display:inline-block;
	top:-30px;
	right:-30px;
}

.closeBtn2 img{
	
	width:20px;
}
.closeBtn2 img:hover{
	cursor:pointer;
}

.cartItemPrice{

	position:relative;
	left:-30px;
	float:right;
	top:25px;
	color:black;
	font-family:Cairo-Light;
	font-weight:600;
	margin-top:-7px;
}

.itemqty{
	position:relative;
	display:inline-block;
	float:left;
	top:-10px;
	font-family:Cairo-Light;
	
}

.checkout{

	margin-bottom:10px;
	position:relative;
	width:200px;
	height:35px;
	background-color:#182C61;
	left:5px;
	margin-top:15px;
	font-family:"Muli";
	font-weight:600;
	color:white;
	padding-top:15px;
	display:inline-block;
}



.checkout:hover{
	cursor:pointer
}

.total{
	color:black;
	font-family:Cairo-Light;
	font-weight:300;
	float:left;
	left:20px;
	position:relative;
	margin-top:5px;
	font-size:20px;
}
